package dev.oniksen.demotelegramminiapp

import com.kirillNay.telegram.miniapp.webApp.ScanQrPopupParams
import com.kirillNay.telegram.miniapp.webApp.webApp

class TelegramMiniAppImpl : TelegramMiniApp {
    override val version: String = webApp.version

    override fun showScanQrPopup(text: String, callback: ((id: String) -> Boolean)?) {
        webApp.showScanQrPopup(
            params = ScanQrPopupParams(
                text = text
            ),
            onData = callback
        )
    }
}