package dev.oniksen.demotelegramminiapp

import androidx.compose.foundation.Image
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.rounded.ShoppingCart
import androidx.compose.material.icons.rounded.Star
import androidx.compose.material3.*
import androidx.compose.runtime.*
import dev.oniksen.composeapp.generated.resources.Res
import dev.oniksen.demotelegramminiapp.dataClasses.NavigationItem
import io.github.alexzhirkevich.compottie.Compottie
import io.github.alexzhirkevich.compottie.LottieCompositionSpec
import io.github.alexzhirkevich.compottie.rememberLottieComposition
import io.github.alexzhirkevich.compottie.rememberLottiePainter
import org.jetbrains.compose.resources.ExperimentalResourceApi

@Composable
fun AppContent() {
    val navListItems = listOf(
        NavigationItem(label = "", icon = Icons.Rounded.Star),
        NavigationItem(label = "", icon = Icons.Rounded.ShoppingCart)
    )
    var selectedIndex by remember { mutableIntStateOf(0) }

    MaterialTheme {
        Scaffold(
            bottomBar = {
                NavigationBar {
                    navListItems.forEachIndexed { index, navigationItem ->
                        NavigationBarItem(
                            selected = selectedIndex == index,
                            onClick = {
                                selectedIndex = index
                            },
                            icon = {
                                Icon(imageVector = navigationItem.icon, contentDescription = "Icon")
                            },
                            label = {
                                Text(text = navigationItem.label)
                            }
                        )
                    }
                }
            }
        ) {
            SelectedPage(selectedIndex)
        }
    }
}

@Composable
fun SelectedPage(selectedIndex: Int) {
    when(selectedIndex) {
        0 -> RequestsPageContent()
        1 -> QrScannerContent(TelegramMiniAppFactory.instance)
    }
}